<script>
   import { qnorm } from 'mdatools/distributions';
   import { ppoints, mean, quantile, mrange } from 'mdatools/stat';
   import { isvector, vector } from 'mdatools/arrays';
   import { Axes, XAxis, YAxis, Box, Lines, Points } from 'svelte-plots-basic/2d';

   export let values;

   export let borderColor = 'blue';
   export let lineColor = 'blue';
   export let markerSize = 1.25;
   export let borderWidth = 2;

   export let lineType = 2;
   export let xLabel = "Sample values";
   export let yLabel = "Normal quantiles, z";

   export let limX = undefined;
   export let limY = undefined;
   export let showGrid = true;
   export let margins = [1, 1, 0.5, 0.5];

   // constants for theoretical line
   const llx = [-4, 4];
   const lp = [0.25, 0.75];
   const lz = [-0.6744898,  0.6744898];
   const dlz = lz[1] - lz[0];

   let sx, sz, lly;
   $: {
      if (Array.isArray(values)) {
         values = vector(values);
      }

      if (values === undefined || !isvector(values) || values.length < 3) {
         throw Error('QQPlot: parameter "values" must be a vector or an array with more than 2 numbers.');
      };

      sx = values.sort();
      sz = qnorm(ppoints(sx.length));

      // theoretical line
      const lx = quantile(sx, lp);
      const la = (lx.v[1] - lx.v[0]) / dlz;
      const lb = mean(lx);
      lly = [-4 * la + lb, 4 * la + lb];
   }
</script>

<Axes {yLabel} {xLabel} limX={limX ? limX : mrange(sz)} limY={limY ? limY : mrange(sx)} {margins}>
   <slot></slot>
   <Lines xValues={llx} yValues={lly} {lineType} {lineColor} />
   <Points xValues={sz} yValues={sx} {borderWidth} {markerSize} {borderColor}/>

   <XAxis slot="xaxis" {showGrid}></XAxis>
   <YAxis slot="yaxis" {showGrid}></YAxis>
   <Box slot="box"></Box>
</Axes>
